import { useState, useEffect } from 'react';

export const useFetch = ({
  fetchFunc,
  apiState,
  firstRenderCheck = false,
  addProp,
  initialValue = null,
  additionalFunc,
}) => {
  const [state, setState] = useState(initialValue);

    // additionalFunc && additionalFunc(state);

  useEffect(() => {
    const abortController = new AbortController();
    const fetch = async () => {
      apiState.pending();
      try {
        const data = await fetchFunc(addProp);
        setState(data);
        apiState.success();
      } catch (error) {
        apiState.error();
      }
    };

    if (firstRenderCheck) {
      if (addProp) {
        fetch();
      }
    } else {
      fetch();
    }


    return () => {
      abortController.abort();
    };
  }, [
    apiState,
    addProp,
    setState,
    fetchFunc,
    firstRenderCheck,
  ]);

  return { state, setState };
};
